/*--------------------------------------------------------------
# general
--------------------------------------------------------------*/

.App {
  background: url(../assests/Subtle-Prism.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #f1f1f1;
}

.content-box {
  background: white;
  padding: 15px;
  width: 95%;
  border-radius: 16px;
  border: 1px solid #e7eaf1;
  margin: 20px auto 0 auto;
}

.logo {
  width: 70%;
  height: auto;
  margin: 0 auto;
  display: block;
}

.btn-outline-primary {
  color: #616e5e;
  border-color: #616e5e;
  border-radius: 10px;
}

.btn-outline-primary:hover {
  background-color: #616e5e;
  color: white;
  border-color: #616e5e;
}

#header {
  padding-top: 100px;
  background: rgb(129, 129, 129);
  background: linear-gradient(248deg, #040b14 0%, #081526 49%, #081526 50%, #0e1a2a 51%, #132439 100%);
}

/*--------------------------------------------------------------
# hero
--------------------------------------------------------------*/

#hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: url('../assests/Hero-removebg 2.png');
  background-repeat: no-repeat;
  background-size: 30%;
  background-position-x: right;
  background-position-y: bottom;
  color: #5a697b;
}

#hero .type-writer-wrapper > div {
  display: inline;
}

#hero .hero-container {
  position: absolute;
  z-index: 2;
  min-width: 300px;
}

#hero h1 {
  margin: 0 0 10px 0;
  font-size: 64px;
  font-weight: 700;
  line-height: 56px;
}

#hero .type-writer-wrapper {
  margin-bottom: 50px;
  font-size: 26px;
  font-family: 'Poppins', sans-serif;
  color: #293544;
}

#hero .type-writer-wrapper span {
  padding-bottom: 4px;
  letter-spacing: 1px;
  border-bottom: 3px solid #149ddd;
}

@media (min-width: 1024px) {
  #hero {
    background-attachment: fixed;
  }
}

@media (max-width: 768px) {
  #hero h1 {
    font-size: 28px;
    line-height: 36px;
  }
  #hero h2 {
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 30px;
  }
}

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
#footer {
  padding: 15px;
  color: #f4f6fd;
  font-size: 14px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 300px;
  z-index: 9999;
}

#footer .copyright {
  text-align: center;
}

#footer .credits {
  padding-top: 5px;
  text-align: center;
  font-size: 13px;
  color: #eaebf0;
}

@media (max-width: 1199px) {
  #footer {
    position: static;
    width: auto;
    padding-right: 20px 15px;
  }
}
