.customCard {
  border-radius: 0;
  border: none;
  overflow: hidden;
  box-shadow: 3px 4px 15px 0px rgb(140 140 140 / 20%);
  transition: 0.2s ease-in-out;
  border: 1px solid #e7eaf1;
}

.customCard:hover {
  box-shadow: 2px 2px 15px 1px rgb(180 180 180 / 80%);
  transition: 0.2s ease-in-out;
}

.customCarusel {
  height: 180px;
  opacity: 0.8;
  transition: 0.3s ease-in-out all;
}

.customCard:hover .customCarusel {
  opacity: 1;
  transition: 0.3s ease-in-out all;
}

.customCarusel > ol {
  justify-content: space-evenly;
  padding: 0 15px;
  bottom: 0;
  opacity: 0;
  animation: fade-out 0.2s forwards;
}

.customCard:hover .customCarusel > ol {
  animation: fade-in 0.2s forwards;
}

.customCarusel > ol > li {
  background-color: rgb(0, 0, 0);
  height: 10px;
  width: 10px;
  border-radius: 50%;
  margin: 0;
  border: none;
  width: 10px;
  height: 10px;
  border-radius: 100%;
}

.cardFooter {
  display: flex;
  justify-content: space-evenly;
  font-size: 1.5em;
  border: none;
}

.cardText {
  overflow: hidden;
  position: relative;
  min-height: 140px;
}

.textShadow {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  margin: 0;
  padding: 30px 0 0 0;
  opacity: 1;
  background-image: linear-gradient(0deg, rgba(255, 255, 255, 1) 25%, rgba(255, 255, 255, 0) 55%);
}

.downIcon {
  display: block;
  cursor: pointer;
  width: 85%;
  color: #149ddd;
  margin: 0 auto;
}

.downIcon:hover {
  transition: 0.2s ease-in-out;
  color: #005b85;
}

.cardGrowAnimation {
  animation: grow-height forwards;
  animation-duration: 0.7s;
}

.cardShrinkAnimation {
  animation: shrink-height forwards;
  animation-duration: 0.7s;
}

.rotateDown {
  transition: 0.3s ease-in-out;
  transform: rotate(180deg);
}

.rotateUp {
  transition: 0.3s ease-in-out;
  transform: rotate(0deg);
}

@keyframes grow-height {
  from {
    max-height: 140px;
  }
  to {
    max-height: 500px;
  }
}

@keyframes shrink-height {
  from {
    max-height: 500px;
  }
  to {
    max-height: 140px;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
    bottom: 0;
  }
  to {
    opacity: 1;
    bottom: -30px;
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
    bottom: -30px;
  }
  75% {
    opacity: 0;
    bottom: -30px;
  }
  100% {
    bottom: -40px;
  }
}
