.customProgressBar {
  height: auto;
  background: gainsboro;
  border-radius: 6px;
  overflow: hidden;
}

.prograssWrapper .progress {
  height: 10px;
}

.prograssWrapper .progress > div {
  height: 10px;
  background-color: #149ddd;
  transition: 2s;
  transition-delay: 0.5s;
}

.skillName {
  padding: 10px 0;
  margin: 0 0 6px 0;
  text-transform: uppercase;
  display: block;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
  color: #050d18;
}
